import { getAdmin } from "../../api/admin";
export default {
  data: function data() {
    return {
      param: {
        username: 'admin',
        password: '123456'
      },
      rules: {
        username: [{
          required: true,
          message: '请输入用户名',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: '请输入密码',
          trigger: 'blur'
        }]
      }
    };
  },
  methods: {
    submitForm: function submitForm() {
      var _this = this;

      this.$refs.login.validate(function (valid) {
        if (valid) {
          getAdmin(_this.param).then(function (res) {
            if (res.code == 2000) {
              _this.$message.success('登录成功');

              localStorage.setItem('ms_username', _this.param.username); // localStorage.setItem('adminInfo', res.data);

              _this.$router.push('/');
            } else if (res.code == 2001) {
              _this.$message.error('用户名或密码错误');

              return;
            }
          });
        } else {
          _this.$message.error('请输入账号和密码');

          console.log('error submit!!');
          return false;
        }
      });
    }
  }
};